@import url(https://fonts.googleapis.com/css?family=Roboto:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

#app {
  background-attachment: fixed;
  background-size: cover;
  background-position: center;
  margin: 0;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

#details {
  background: rgba(255, 255, 255, 0.9);
  border-radius: 7px;
  height: 90vh;
  /* margin-left: 35px !important; */
}

#sidebar {
  background: rgba(255, 255, 255, 0.9);
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  height: 90vh;
  width: 235px !important;
  overflow-x: hidden;
}

#personal-lists {
  -ms-overflow-style: none;
  overflow: scroll;
  flex-grow: 1;
}

#personal-lists::-webkit-scrollbar {
  display: none;
}

.todolist {
  height: 100vh;
  border: solid 0.25px rgb(240, 240, 240);
  border-radius: 7px;
  background: rgba(255, 255, 255, 0.9);
  height: 90vh;
  /* margin-left: 17.5px !important; */
}

.todolist-items {
  height: 71vh;
  overflow-x: hidden;
  overflow-y: auto;
}

#addItemInput {
  cursor: text;
  background-color: rgba(0, 0, 0, 0);
  border: 0;
}

[contenteditable]:focus {
  outline: 0px solid transparent;
}

.editable:empty:before {
  content: attr(data-placeholder);
}

::-webkit-scrollbar-track {
  opacity: 0;
}
/* Hide scrollbar for IE and Edge */
.todolist-items {
  -ms-overflow-style: none;
}

.font-small {
  font-size: 12px;
}

.icon {
  font-size: 14px;
  transition: 0.3s ease;
  border: 0;
  background-color: rgba(0, 0, 0, 0);
  border-left: 0.25px rgba(187, 187, 187, 0.4) solid;
}

.completed {
  opacity: 0.3;
}

.list-item {
  border-bottom: 0.25px rgb(187, 187, 187, 0.4) solid;
  box-sizing: border-box;
}

.title-input {
  width: 100%;
  text-overflow: ellipsis;
  border: 0;
  background: rgba(255, 255, 255, 0);
}

input:focus,
textarea:focus,
select:focus {
  outline: none !important;
}

.icon:hover {
  transition: 0.3s ease;
  cursor: pointer;
  background-color: rgb(204, 207, 211);
}

.calendar-overlay-button {
  width: 100%;
}

.calendar-overlay {
  position: absolute;
  z-index: 1000;
  background-color: white;
  border: solid 0.25px rgb(240, 240, 240);
  border-radius: 7px;
  padding: 10px;
  width: 250px;
  /* box-shadow: 0 1px 0px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12); */
}

.priorities-overlay {
  position: absolute;
  top: 50px;
  z-index: 1000;
  background-color: white;
  border: solid 0.25px rgb(240, 240, 240);
  padding: 0px;
  border-radius: 7px;
  width: 70px;
  font-size: 13px;
  overflow: hidden;
  /* box-shadow: 0 1px 1px rgba(0, 0, 0, 0.034), 0 2px 1px rgba(0, 0, 0, 0.048),
    0 3px 2px rgba(0, 0, 0, 0.06), 0 6px 4px rgba(0, 0, 0, 0.072),
    0 10px 8px rgba(0, 0, 0, 0.086), 0 60px 40px rgba(0, 0, 0, 0.12); */
}

.priorities-overlay-item {
  transition: 0.15s ease;
  padding: 5px;
}

.delete-menu {
  border-radius: 5px;
  z-index: 1000;
  color: rgb(73, 80, 87);
  background-color: white;
  border: solid 0.25px rgb(240, 240, 240);
  transition: 0.1s ease;
  font-size: 13px;
}

.delete-menu:hover {
  transition: 0.2s ease;
  cursor: pointer;
  background-color: rgb(240, 240, 240);
}

.sidebar-context-menu {
  border-radius: 5px;
  z-index: 1000;
  color: rgb(73, 80, 87);
  background-color: white;
  border: solid 0.25px rgb(240, 240, 240);
  transition: 0.1s ease;
  font-size: 13px;
}

.sidebar-context-menu-item:hover {
  transition: 0.2s ease;
  cursor: pointer;
  background-color: rgb(240, 240, 240);
}

.priorities-overlay-item:hover {
  transition: 0.15s ease;
  cursor: pointer;
  background-color: rgb(240, 240, 240);
}

.calendarOverlay {
  width: 200px;
  height: 200px;
}

[placeholder]:empty::before {
  content: attr(placeholder);
  color: #555;
}

[placeholder]:empty:focus::before {
  content: "";
}

.calendar-base {
  transition: 0.3s ease;
}

.priorities-base {
  transition: 0.3s ease;
}

.text-warning {
  transition: 0.3s ease;
}

.text-danger {
  transition: 0.3s ease;
}

.text-primary {
  transition: 0.3s ease;
}

.calendar-selected {
  color: rgb(21, 127, 251);
  transition: 0.3s ease;
}

.base {
  transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
}

.icon-rotated {
  transform: rotate(-90deg);
  -webkit-transition: all 0.2s ease;
  -moz-transition: all 0.2s ease;
  -o-transition: all 0.2s ease;
  -ms-transition: all 0.2s ease;
  transform-origin: center;
  transition: all 0.2s ease;
}

.todo-item {
  transition: 0.2s ease;
  border-right: 2px solid rgb(21, 127, 251);
}

.selected-item {
  transition: 0.2s ease;
  background-color: rgba(216, 214, 214, 0.5) !important;

  box-sizing: border-box;
}

.selected-item-input {
  /* transition: 0.1s ease; */
  background-color: rgb(240, 240, 240, 0) !important;
}

.details-title-input {
  width: 100%;
  text-overflow: ellipsis;
  overflow-wrap: anywhere;
  border: 0;
  font-weight: 700;
  font-size: 16px;
  background-color: rgba(0, 0, 0, 0);
}

#details-top-row {
  border-bottom: 0.25px rgb(187, 187, 187, 0.4) solid;
}

.details-top-row-check {
  border-right: 0.25px rgb(187, 187, 187, 0.4) solid;
}

#details-desc-input {
  width: 100%;
  height: 100%;
  border: 0;
  font-size: 14px;
  resize: none;
  background-color: rgba(0, 0, 0, 0);
}

#details-desc-input-container {
  height: 70%;
}

.details-date-picker {
  font-size: 14px;
  color: rgb(73, 80, 87);
  transition: ease 0.2s;
}

.details-priority-picker {
  font-size: 14px;
  color: rgb(73, 80, 87);
  transition: ease 0.2s;
}

.details-date-picker:hover {
  cursor: pointer;
  transition: ease 0.2s;
  opacity: 0.7;
}

.details-priority-picker:hover {
  cursor: pointer;
  transition: ease 0.2s;
  opacity: 0.7;
}

#details-placeholder {
  font-size: 16px;
  color: rgb(73, 80, 87);
  pointer-events: none;
}

.details-container {
  height: 95vh;
}

.form-check-input {
  margin-top: 0.58rem !important;
}

.details-delete-btn {
  color: rgb(73, 80, 87);
  font-size: 14px;
  border-left: 0.25px rgb(187, 187, 187, 0.4) solid;
  transition: ease 0.2s;
}

.details-delete-btn:hover {
  cursor: pointer;
  color: rgb(130, 142, 153);
  transition: ease 0.2s;
}

.email-check {
  color: rgb(48, 166, 74);
}

input:focus,
textarea:focus,
select:focus {
  outline: none;
}

#toolbar {
  position: absolute;
  width: 100px !important;
  top: 1.5em;
  right: 1.5em;
  padding-left: 30px;
  height: 35%;
}

.toolbar-icon {
  color: rgba(255, 255, 255, 0.75);
  font-size: 20px;
  transition: 0.2s ease;
  text-shadow: 2px 4px 3px rgba(0, 0, 0, 0.1);
}

.toolbar-icon:hover {
  color: rgba(255, 255, 255, 1);
  transition: 0.2s ease;
  cursor: pointer;
  transform: scale(1.2, 1.2);
}

.sidebar-item {
  border-bottom: 0.25px rgb(187, 187, 187, 0.4) solid;
  box-sizing: border-box;
  color: rgb(73, 80, 87);
  font-size: 14px;
  transition: 0.2s ease;
  background-color: rgba(0, 0, 0, 0);
}

.sidebar-icon {
  /* margin-right: 10px; */
  color: rgb(113, 123, 133);
}

.list-name {
  pointer-events: none;
}

.sidebar-item:hover {
  cursor: pointer;
  transition: 0.2s ease;
  background-color: rgba(216, 214, 214, 0.5) !important;
}

.selected-sidebar-item {
  background-color: rgb(21, 127, 251);
  color: white;
}

.selected-sidebar-item:hover {
  background-color: rgb(21, 127, 251) !important;
  color: white !important;
}

.selected-sidebar-item > .sidebar-icon {
  color: white;
}

.sidebar-count {
  font-size: 9px;
}

.notification {
  font-size: 14px;
  width: 300px;
  height: 90px;
  border-radius: 7px;
  position: absolute;
  bottom: 20px;
  right: -300px;
  z-index: 1000;
  background-color: white;
  transition: 0.5s ease-in-out;
  color: rgb(73, 80, 87);
  opacity: 0;
}

.notification-displayed {
  transition: 0.5s ease-in-out;
  right: 20px;
  opacity: 1;
}

.undo-button {
  transition: 0.2s ease;
}

.undo-button:hover {
  color: rgb(40, 40, 131) !important;
  transition: 0.2s ease;
}

.add-list-overlay {
  width: 400px;
  height: 300px;
  position: absolute;
  z-index: 1000;
  left: 50%;
  top: 50%;
  margin-left: -200px;
  margin-top: 50vh;
  background-color: white;
  border-radius: 7px;
  transition: 0.35s ease-in-out;
  opacity: 0;
}

.add-list-overlay-displayed {
  transition: 0.35s ease-in-out;
  margin-top: -150px;
  opacity: 1;
}

.add-list-overlay-color {
  border-radius: 50%;
  /* box-sizing: border-box; */
  transition: 0.2s ease;
  background-color: rgb(0, 0, 0, 0);
  border: 1.5px rgb(0, 0, 0, 0) solid;
  transform: scale(1.01, 1.01);
  transform-origin: center;
}

.add-list-overlay-color:hover {
  transform: scale(1.5, 1.5);
}

.add-list-overlay-color-selected {
  transition: 0.2s ease;
  border-radius: 50%;
  /* box-sizing: border-box; */
  /* background-color: rgb(104, 113, 122) !important; */
  /* border: 1px rgb(104, 113, 122) solid !important; */
}

.add-list-overlay-color-selected::after {
  /* content: "x"; */
}

.details-list-name {
  position: absolute;
  left: 27px;
  bottom: 20px;
  font-size: 15px;
}

.btn-secondary {
  background-color: rgb(98, 102, 107) !important;
}

.btn-secondary:hover {
  background-color: rgb(86, 92, 99) !important;
}

#login-register {
  height: 100vh;
  background-attachment: fixed;
  background-size: cover;
  background-position: center;

  padding: 0 !important;
}

.Login {
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  height: 87vh;
  background-color: rgba(255, 255, 255, 0.85);
}

.Register {
  border-top-right-radius: 7px;
  border-bottom-right-radius: 7px;
  height: 87vh;
  width: 400px;
  background-color: rgba(255, 255, 255, 0.85);
  overflow: hidden;
}
.login-section {
  width: 400px;
  float: left;
}

.register-section {
  width: 74vw !important;
  float: right;
}

.toggle-prompt-text {
  transition: 0.2s ease;
}

.toggle-prompt-text:hover {
  transition: 0.2s ease;
  color: rgb(40, 40, 109) !important;
}

.font-grey {
  color: rgb(73, 80, 87);
}

.info-icon {
  color: rgb(122, 134, 146) !important;
}

.font-small-login-reg {
  font-size: 14px;
}

.btn {
  padding-top: 3px !important;
  padding-bottom: 3px !important;
}

.or-separator {
  display: flex;
  align-items: center;
  text-align: center;
  color: rgb(122, 131, 139);
  font-size: 12px;
}
.or-separator::before,
.or-separator::after {
  content: "";
  flex: 1 1;
  border-bottom: 1px solid rgb(178, 186, 194) !important;
}
.or-separator::before {
  margin-right: 0.25em;
}
.or-separator::after {
  margin-left: 0.25em;
}

input {
  border-radius: 4px !important;
}

.input-validated {
  border-top-left-radius: 4px !important;
  border-bottom-left-radius: 4px !important;
  border-top-right-radius: 0px !important;
  border-bottom-right-radius: 0px !important;
}

/* .profile-icon {
  position: absolute;
  left: 100px;
} */

.google-btn {
  width: 184px;
  height: 42px;
  background-color: #4285f4;
  border-radius: 2px;
  box-shadow: 0 3px 4px 0 rgba(0, 0, 0, 0.25);
  transition: 0.2s ease;
}

.google-btn-lg {
  width: 195px;
}

.google-btn .google-icon-wrapper {
  position: absolute;
  margin-top: 1px;
  margin-left: 1px;
  width: 40px;
  height: 40px;
  border-radius: 2px;
  background-color: #fff;
}
.google-btn .google-icon {
  position: absolute;
  margin-top: 11px;
  margin-left: 11px;
  width: 18px;
  height: 18px;
}
.google-btn .btn-text {
  float: right;
  margin: 11px 11px 0 0;
  color: #fff;
  font-size: 14px;
  letter-spacing: 0.2px;
  font-family: "Roboto";
}
.google-btn:hover {
  box-shadow: 0 0 6px #4285f4;
}
.google-btn:active {
  transition: 0.2s ease;
  background: #1669f2;
}

no-bg-no-borders {
  background-color: rgba(0, 0, 0, 0) !important;
  border: 0 !important;
}

